body {
    background: #ECEFF1;
    color: rgba(0, 0, 0, 0.87);
    font-family: Roboto, Helvetica, Arial, sans-serif;
    margin: 0;
    padding: 0;
}

#message {
    background: white;
    max-width: 400px;
    margin: 100px auto 16px;
    padding: 32px 24px 16px;
    border-radius: 10px;
    
}

#message h3 {
    color: #888;
    font-weight: large;
    font-size: 36px;
    margin: 16px 0 12px;
}

#message h2 {
    color: #ffa100;
    font-weight: bold;
    font-size: 20px;
    margin: 0 0 8px;
}

#message h1 {
    font-size: 36px;
    font-weight: 300;
    color: rgba(0, 0, 0, 0.6);
    margin: 0 0 16px;
}

#message p {
    line-height: 140%;
    margin: 16px 0 24px;
    font-size: 14px;
}



#message,
#message a {
    box-shadow: 5px 5px 15px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
}



@media (max-width: 600px) {

    body,
    #message {
        margin-top: 0;
        background: white;
        box-shadow: none;
    }

    body {
        border-top: 16px solid #ffa100;
    }}